.coaches-slide {
  position: relative;
  margin: 0 auto;
  cursor: grab;
  z-index: 5;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  flex-shrink: 0;
  padding: 0;
  max-height: 294px;
  color: #ffffff;
  transition-property: transform;
  background-color: #f8f9fc;
  background-position: center;
  background-size: 100%;
}
.coaches-slide::after {
  position: absolute;
  content: attr(data-name);
  display: inline-block;
  width: max-content;
  z-index: 5;
  bottom: 8%;
  right: 0;
  left: 0;
  margin: 0 auto;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .coaches-slide {
    max-height: 100%;
  }
}
.coaches-slide:hover .coaches__description, .coaches-slide:focus-visible .coaches__description {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  transform: translateY(0%);
  background-color: #102654;
  opacity: 0.9;
  transition: all 0.5s ease;
}
.coaches-slide:hover .coaches__description span, .coaches-slide:focus-visible .coaches__description span {
  position: static;
  display: block;
  text-align: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
}
.coaches-slide:hover::after, .coaches-slide:focus-visible::after {
  display: none;
}
.coaches-slide img {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  object-fit: cover;
}

