// Colors
// ---------------------------------

// Default
$color-default-black: #000000;
$color-default-sapphire: #102654;
$color-default-sapphire-opacity: rgba(16, 38, 84, 0.2);
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue: #2c39f2;
$color-egyptian-blue: #103aae;
$color-resolution-blue: #1c3374;
$color-persian-blue: #2e35ac;
$color-royal-blue: #3367d5;
$color-smalt: #0041ca;
$color-gulf-blue: #2a3b63;

$color-torch-red: #ed0233;
$color-torch-red-second: #ff121f;
$color-crimson: #d10a42;
$color-ghost-white: #f8f9fc;
$color-black-russian: #030a1a;
$color-whisper: #e8e8e8;
$color-grey: #787878;
$color-link-water: #c8c9cc;
$color-light-slate-grey: #74819c;
$color-bright-grey: #4f545f;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$torch-red-to-cobalt: linear-gradient(90deg, #ed0233 30%, #003eb7 70%);
$btn-torch-red-to-cobalt: linear-gradient(120deg, #ed0233, #003eb7);
$torch-crimson-to-cobalt: linear-gradient(155deg, #d10a42 22%, #0041ca 50%);
$torch-crimson-to-smalt: linear-gradient(160deg, #d10a42 20%, #0041ca 75%);

// Typography
// ---------------------------------

//    html   
$fz-default: 16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;

$ff-placeholder: "Placeholder", "Arial", sans-serif;
$ff-primary: "Alumni Sans", "Arial", sans-serif;
$ff-secondary: "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1200: 1200px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
